'use client'

import { useEffect, useState } from 'react'
import { motion, AnimatePresence } from 'framer-motion'
import { Menu, X } from 'lucide-react'
import { Button } from '@/components/ui/button'
import { aboutAPI, infoCardsAPI, eventsAPI, menuItemsAPI, heroButtonsAPI, imagesAPI, contactAPI } from '@/lib/api-client'

export default function HomePage() {
  const [aboutContent, setAboutContent] = useState<any>(null)
  const [infoCards, setInfoCards] = useState<any[]>([])
  const [events, setEvents] = useState<any[]>([])
  const [menuItems, setMenuItems] = useState<any[]>([])
  const [heroButtons, setHeroButtons] = useState<any[]>([])
  const [heroImages, setHeroImages] = useState<any[]>([])
  const [contactInfo, setContactInfo] = useState<any[]>([])
  const [loading, setLoading] = useState(true)
  const [isScrolled, setIsScrolled] = useState(false)
  const [mobileMenuOpen, setMobileMenuOpen] = useState(false)

  useEffect(() => {
    loadContent()
  }, [])

  useEffect(() => {
    const handleScroll = () => {
      setIsScrolled(window.scrollY > 50)
    }
    window.addEventListener('scroll', handleScroll)
    return () => window.removeEventListener('scroll', handleScroll)
  }, [])

  const loadContent = async () => {
    try {
      const [about, cards, eventsData, menu, hero, heroImgs, contact] = await Promise.all([
        aboutAPI.get(),
        infoCardsAPI.getAll(),
        eventsAPI.getAll(),
        menuItemsAPI.getAll(),
        heroButtonsAPI.getAll(),
        imagesAPI.getByCategory('hero'),
        contactAPI.getAll(),
      ])

      setAboutContent(about)
      setInfoCards(cards)
      setEvents(eventsData)
      setMenuItems(menu)
      setHeroButtons(hero)
      setHeroImages(heroImgs)
      setContactInfo(contact)
    } catch (error) {
      console.error('Error loading content:', error)
    } finally {
      setLoading(false)
    }
  }

  if (loading) {
    return (
      <div className="min-h-screen flex items-center justify-center bg-ashram-sand">
        <div className="text-2xl text-ashram-clay">Loading...</div>
      </div>
    )
  }

  return (
    <div className="min-h-screen bg-ashram-sand">
      {/* Hero Section */}
      <section className="py-20 px-4">
        <div className="max-w-7xl mx-auto text-center">
          <h1 className="text-6xl font-bold text-ashram-clay mb-6 font-serif">
            {aboutContent?.heroTitle || 'Gujarat Ashram'}
          </h1>
          <h2 className="text-3xl text-ashram-stone mb-4">
            {aboutContent?.heroSubtitle || 'Art of Living'}
          </h2>
          <p className="text-xl text-ashram-stone max-w-3xl mx-auto">
            {aboutContent?.heroDescription || 'Welcome to our ashram'}
          </p>
        </div>
      </section>

      {/* Info Cards Section */}
      {infoCards.length > 0 && (
        <section className="py-16 px-4 bg-white">
          <div className="max-w-7xl mx-auto">
            <h2 className="text-4xl font-bold text-ashram-clay mb-12 text-center font-serif">
              {aboutContent?.aboutTitle || 'Our Offerings'}
            </h2>
            <div className="grid md:grid-cols-3 gap-8">
              {infoCards.map((card) => (
                <div
                  key={card.id}
                  className="p-6 rounded-lg border-2 border-ashram-amber/20 bg-ashram-sand/50"
                >
                  <h3 className="text-2xl font-bold text-ashram-clay mb-3">
                    {card.title}
                  </h3>
                  <p className="text-ashram-stone">{card.description}</p>
                </div>
              ))}
            </div>
          </div>
        </section>
      )}

      {/* Events Section */}
      {events.length > 0 && (
        <section className="py-16 px-4">
          <div className="max-w-7xl mx-auto">
            <h2 className="text-4xl font-bold text-ashram-clay mb-12 text-center font-serif">
              Upcoming Programs
            </h2>
            <div className="grid md:grid-cols-3 gap-8">
              {events.map((event) => (
                <div
                  key={event.id}
                  className="bg-white p-6 rounded-lg border-2 border-ashram-amber/20"
                >
                  <h3 className="text-2xl font-bold text-ashram-clay mb-2">
                    {event.title}
                  </h3>
                  <p className="text-ashram-amber mb-3">{event.date}</p>
                  <p className="text-ashram-stone">{event.description}</p>
                </div>
              ))}
            </div>
          </div>
        </section>
      )}

      {/* Footer */}
      <footer className="bg-ashram-clay text-white py-12 px-4">
        <div className="max-w-7xl mx-auto text-center">
          <h3 className="text-3xl font-bold mb-4 font-serif">
            {aboutContent?.footerTitle || 'Gujarat Ashram'}
          </h3>
          <p className="text-ashram-sand/80 max-w-2xl mx-auto">
            {aboutContent?.footerDescription || 'A sanctuary for peace and spiritual growth'}
          </p>
        </div>
      </footer>

      {/* Migration Status */}
      <div className="fixed bottom-4 right-4 bg-ashram-green text-white px-6 py-3 rounded-lg shadow-lg">
        <p className="font-semibold">Next.js Migration - Phase 3 Complete</p>
        <p className="text-sm">All API routes working!</p>
      </div>
    </div>
  )
}
